.controls-frame {
    margin: 0 auto;
    padding: 0;
    max-width: 880px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.controls-frame__navigation {
    margin: 0;
    padding: 0;
    width: 100%;
    box-sizing: border-box;
}

.controls-frame__navigation-list {
    margin: 0;
    padding: 20px 20px;
    width: 100%;
    box-sizing: border-box;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 1023px) {
    .controls-frame__navigation-list {
        padding: 10px 10px;
    }
}

@media screen and (max-width: 767px) {
    .controls-frame__navigation-list {
        margin: 0 auto;
        max-width: 560px;
    }
}

@media screen and (max-width: 550px) {
    .controls-frame__navigation-list {
        margin: 0 auto;
        max-width: 380px;
    }
}

.controls-frame__frame {
    margin: 20px 0 0;
    padding: 0;
    width: 800px;
    height: 600px;
    position: relative;
    border: 2px solid var(--color-light);
    box-shadow: 3px 3px 10px var(--color-light);
    overflow: hidden;
}

@media screen and (max-width: 1023px) {
    .controls-frame__frame {
        margin: 15px 0 0;
        width: 690px;
        height: 480px;
    }
}

@media screen and (max-width: 767px) {
    .controls-frame__frame {
        margin: 15px 0 0;
        width: 500px;
        height: 350px;
    }
}

@media screen and (max-width: 550px) {
    .controls-frame__frame {
        margin: 15px 0 0;
        width: 350px;
        height: 450px;
    }
}

.controls-buttons {
    margin: 0 auto;
    padding: 0;
    max-width: 320px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.controls-buttons__navigation {
    margin: 0;
    padding: 0;
    width: 100%;
    box-sizing: border-box;
}

.controls-buttons__navigation-list {
    margin: 0;
    padding: 20px 0;
    width: 320px;
    box-sizing: border-box;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (min-width: 550px) {
    .controls-buttons {
        display: none;
    }

    .controls-buttons__navigation-list {
        display: none;
    }
}